<template>
  <div>
     <div class="banner" @click="handleBannerClick">
      <img class="banner-img" :src="bannerImg" alt="">
      <div class="banner-info">
        <div class="banner-title">{{sightName}}</div>
        <div class="banner-num">
          <span class="iconfont banner-icon">&#xe692;</span>
          {{this.gallaryImgs.length}}
        </div>
      </div>
    </div>
    <fade-animation>
      <common-gallayr :imgs="gallaryImgs" v-show="showGallary" @hide="handleHide"></common-gallayr>
    </fade-animation>
  </div>
 
</template>

<script>
import CommonGallayr from 'common/gallary/Gallary'
import FadeAnimation from 'common/fade/FadeAnimation'
export default {
  name: 'DetailBanner',
  props: {
    sightName: String,
    bannerImg: String,
    gallaryImgs: Array
  },
  components: {
    CommonGallayr,
    FadeAnimation
  },
  data () {
    return {
      showGallary: false
    }
  },
  methods: {
    handleBannerClick () {
      this.showGallary = true
    },
    handleHide (e) {
      this.showGallary = e
    }
  }
}
</script>

<style lang="stylus" scoped>
  .banner
    overflow:hidden
    height:0
    padding-bottom:55%
    position:relative
    .banner-img
      width:100%
    .banner-info
      display:flex
      position:absolute
      left:0
      right:0
      bottom:0
      line-height:.6rem
      color:#fff
      background-image:linear-gradient(top,rgba(0,0,0,0),rgba(0,0,0,.8))
      .banner-title
        flex:1
        font-size:.32rem
        padding:0 .2rem
      .banner-num
        height:.32rem
        line-height:.32rem
        padding:0 .4rem
        margin-top:.14rem
        border-radius:.2rem
        background:rgba(0,0,0,.8)
        font-size:.24rem
        .banner-icon
          font-size:.24rem
</style>


