<template>
	<div>
    <router-link tag="div" to="/" class="header-abs" v-show="showAbs">
      <div class="iconfont back-icon">&#xe624;</div>
    </router-link>
    <div class="header-fixed" v-show="!showAbs" :style="opacityStyle">
      景点详情
      <router-link to="/">
        <div class="iconfont header-back">&#xe624;</div>
      </router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: 'DetailHeader',
  data () {
    return {
      showAbs: true,
      opacityStyle: {
        opacity: 0
      }
    }
  },
  methods: {
    handleScroll () {
      const top = document.documentElement.scrollTop
      if (top > 60) {
        let opacity = top / 140
        opacity = opacity > 1 ? 1 : opacity
        this.opacityStyle = { opacity }
        this.showAbs = false
      } else {
        this.showAbs = true
      }
    }
  },
  mounted () {
    window.addEventListener('scroll', this.handleScroll)
  },
  unmounted () {
    window.removeEventListener('scroll', this.handleScroll)
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/varibles.styl'
  .header-abs
    position:absolute
    left:.2rem
    top:.2rem
    width:.8rem
    height:.8rem
    line-height:.8rem
    text-align:center
    border-radius:50%
    background:rgba(0,0,0,.5)
    .back-icon
      font-size:.24rem
      color:#fff
  .header-fixed
    position:fixed
    top:0
    left:0
    right:0
    z-index:2
    height:$headerHeight
    line-height:$headerHeight
    overflow:hidden
    font-size:.32rem
    text-align:center
    color:#fff
    background:$bgColor
    .header-back
      position:absolute
      left:0
      top:0
      width:.64rem
      text-align:center
      font-size:.4rem
      color:#fff
</style>


